.flashcards-page {
  min-height: calc(100vh - 4rem);
  background: #f9fafb;
}

.page-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem 1rem;
}

/* Page Header */
.page-header {
  text-align: center;
  margin-bottom: 2rem;
}

.page-header h1 {
  margin: 0 0 0.5rem 0;
  font-size: 2.5rem;
  font-weight: 700;
  color: #1f2937;
}

.page-subtitle {
  margin: 0;
  color: #6b7280;
  font-size: 1.125rem;
}

/* Selection Info */
.selection-info {
  background: white;
  border-radius: 0.75rem;
  padding: 1.5rem;
  margin-bottom: 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
  border: 1px solid #e5e7eb;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 1rem;
}

.selection-summary {
  flex: 1;
}

.selection-summary h3 {
  margin: 0 0 0.75rem 0;
  font-size: 1.25rem;
  font-weight: 600;
  color: #1f2937;
}

.selection-description {
  margin: 0 0 0.5rem 0;
  color: #374151;
  font-size: 1rem;
  font-weight: 500;
}

.selection-stats {
  margin: 0;
  color: #6b7280;
  font-size: 0.875rem;
}

.settings-btn {
  background: #f3f4f6;
  border: 1px solid #d1d5db;
  border-radius: 0.5rem;
  padding: 0.75rem;
  font-size: 1.25rem;
  cursor: pointer;
  transition: all 0.2s ease;
  color: #374151;
  min-width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.settings-btn:hover {
  background: #e5e7eb;
  border-color: #9ca3af;
}

/* Daily Progress */
.daily-progress {
  background: white;
  border-radius: 0.75rem;
  padding: 1.5rem;
  margin-bottom: 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
  border: 1px solid #e5e7eb;
}

.progress-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.progress-header h3 {
  margin: 0;
  font-size: 1.25rem;
  font-weight: 600;
  color: #1f2937;
}

.progress-text {
  font-weight: 500;
  color: #374151;
}

.progress-bar-container {
  background: #f3f4f6;
  border-radius: 0.5rem;
  height: 0.75rem;
  overflow: hidden;
  margin-bottom: 1rem;
}

.progress-bar {
  height: 100%;
  transition: width 0.3s ease;
  background: linear-gradient(90deg, #3b82f6, #1d4ed8);
  border-radius: 0.5rem;
}

.goal-completed {
  text-align: center;
  padding: 0.75rem;
  background: #dcfce7;
  border: 1px solid #bbf7d0;
  border-radius: 0.5rem;
  color: #166534;
  font-weight: 600;
}

/* Quick Actions */
.quick-actions {
  background: white;
  border-radius: 0.75rem;
  padding: 1.5rem;
  margin-bottom: 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
  border: 1px solid #e5e7eb;
}

.quick-actions h3 {
  margin: 0 0 1rem 0;
  font-size: 1.25rem;
  font-weight: 600;
  color: #1f2937;
}

.action-buttons {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.action-btn {
  background: white;
  border: 2px solid #e5e7eb;
  border-radius: 0.75rem;
  padding: 1rem;
  cursor: pointer;
  transition: all 0.2s ease;
  text-align: left;
  width: 100%;
}

.action-btn:hover:not(:disabled) {
  border-color: #d1d5db;
  transform: translateY(-1px);
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
}

.action-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

.action-btn.primary {
  background: #3b82f6;
  border-color: #3b82f6;
  color: white;
}

.action-btn.primary:hover:not(:disabled) {
  background: #2563eb;
  border-color: #2563eb;
}

.action-btn.secondary {
  background: #10b981;
  border-color: #10b981;
  color: white;
}

.action-btn.secondary:hover:not(:disabled) {
  background: #059669;
  border-color: #059669;
}

.action-btn.tertiary {
  background: #f3f4f6;
  border-color: #d1d5db;
  color: #374151;
}

.action-btn.tertiary:hover:not(:disabled) {
  background: #e5e7eb;
  border-color: #9ca3af;
}

.btn-content {
  display: flex;
  align-items: center;
  gap: 0.75rem;
}

.btn-icon {
  font-size: 1.5rem;
  flex-shrink: 0;
}

.btn-text {
  display: flex;
  flex-direction: column;
}

.btn-title {
  font-weight: 600;
  margin-bottom: 0.25rem;
}

.btn-subtitle {
  font-size: 0.875rem;
  opacity: 0.8;
}

/* Daily Stats */
.daily-stats {
  background: white;
  border-radius: 0.75rem;
  padding: 1.5rem;
  margin-bottom: 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
  border: 1px solid #e5e7eb;
}

.daily-stats h3 {
  margin: 0 0 1rem 0;
  font-size: 1.25rem;
  font-weight: 600;
  color: #1f2937;
}

.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  gap: 1rem;
}

.stat-card {
  text-align: center;
  padding: 1rem;
  background: #f9fafb;
  border-radius: 0.5rem;
  border: 1px solid #e5e7eb;
}

.stat-card.extra {
  background: linear-gradient(135deg, #fef3c7, #fde047);
  border-color: #fbbf24;
}

.stat-number {
  display: block;
  font-size: 1.875rem;
  font-weight: 700;
  color: #1f2937;
  line-height: 1;
  margin-bottom: 0.25rem;
}

.stat-label {
  display: block;
  font-size: 0.875rem;
  color: #6b7280;
  font-weight: 500;
}

/* Card Breakdown */
.card-breakdown {
  background: white;
  border-radius: 0.75rem;
  padding: 1.5rem;
  margin-bottom: 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
  border: 1px solid #e5e7eb;
}

.card-breakdown h3 {
  margin: 0 0 1rem 0;
  font-size: 1.25rem;
  font-weight: 600;
  color: #1f2937;
}

.breakdown-stats {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
}

.breakdown-item {
  flex: 1;
  min-width: 120px;
  text-align: center;
  padding: 1rem;
  border-radius: 0.5rem;
  border: 1px solid;
}

.breakdown-item.due {
  background: #fef2f2;
  border-color: #fecaca;
  color: #991b1b;
}

.breakdown-item.learning {
  background: #fffbeb;
  border-color: #fed7aa;
  color: #92400e;
}

.breakdown-item.new {
  background: #f0f9ff;
  border-color: #bae6fd;
  color: #1e40af;
}

.breakdown-count {
  display: block;
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 0.25rem;
}

.breakdown-label {
  display: block;
  font-size: 0.875rem;
  font-weight: 500;
}

/* Responsive Design */
@media (max-width: 768px) {
  .page-container {
    padding: 1rem 0.75rem;
  }

  .page-header h1 {
    font-size: 2rem;
  }

  .page-subtitle {
    font-size: 1rem;
  }

  .selection-info {
    flex-direction: column;
    align-items: stretch;
  }

  .settings-btn {
    align-self: flex-end;
    margin-top: 0.5rem;
  }

  .stats-grid {
    grid-template-columns: repeat(2, 1fr);
  }

  .breakdown-stats {
    flex-direction: column;
  }

  .breakdown-item {
    min-width: auto;
  }

  .action-buttons {
    gap: 0.5rem;
  }

  .btn-content {
    gap: 0.5rem;
  }

  .btn-icon {
    font-size: 1.25rem;
  }

  .btn-title {
    font-size: 0.9rem;
  }

  .btn-subtitle {
    font-size: 0.8rem;
  }
} 